<script setup>
import { ref, onMounted } from 'vue'
import { onShow } from '@dcloudio/uni-app'

// 统计信息
const stats = ref({
  spotCount: 0,
  favoriteCount: 0,
  visitCount: 0
})

// 页面显示时
onShow(() => {
  loadStats()
})

// 页面挂载时
onMounted(() => {
  // 初始化操作
})

/**
 * 加载统计信息
 */
const loadStats = async () => {
  try {
    // 模拟API请求 - 实际项目中替换为真实API
    const response = await mockGetStats()
    if (response.success) {
      stats.value = response.data
    }
  } catch (error) {
    console.error('加载统计信息失败:', error)
  }
}

/**
 * 跳转到我的钓点
 */
const goToMySpots = () => {
  uni.showToast({
    title: '功能开发中',
    icon: 'none'
  })
}

/**
 * 跳转到收藏钓点
 */
const goToFavorites = () => {
  uni.showToast({
    title: '功能开发中',
    icon: 'none'
  })
}

/**
 * 跳转到历史记录
 */
const goToHistory = () => {
  uni.showToast({
    title: '功能开发中',
    icon: 'none'
  })
}

/**
 * 跳转到设置
 */
const goToSettings = () => {
  uni.showToast({
    title: '功能开发中',
    icon: 'none'
  })
}

/**
 * 跳转到关于我们
 */
const goToAbout = () => {
  uni.showToast({
    title: '功能开发中',
    icon: 'none'
  })
}

/**
 * 模拟API请求 - 获取统计信息
 */
const mockGetStats = () => {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve({
        success: true,
        data: {
          spotCount: 12,
          favoriteCount: 8,
          visitCount: 156
        }
      })
    }, 300)
  })
}
</script>

